.Questions {
    display: flex;
    flex-direction: column;
    margin-top: 4rem;
}

.question {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: white;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 5px 15px 0px;
    width: 80%;
    margin: 0 auto;
    margin-bottom: 3rem;
    transition: transform 0.4s;
}

.question:hover {
    transform: scale(1.1);
}

.question__profile {
    margin-top: 1.5rem;
    display: flex;
    align-items: center;
    margin-bottom: 1rem;
}

.question__profile>h4 {
    margin-left: 1rem;
}

.question__info {
    display: flex;
    flex-direction: column;
}

.question__question {
    margin-bottom: 1rem;
}

.question__stats {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin-bottom: 1rem;
}

.likes,
.dislikes {
    display: flex;
    align-items: center;
}

.likes:hover {
    color: blue;
}

.dislikes:hover {
    color: red;
}

.comments:hover {
    color: greenyellow;
}